/**********预设值************/
body{
	/*overflow: hidden;*/
}
html,body,h1,h2,h3,h4,h5,h6,ul,li,ol,span,p,div,img,a,button{
	margin: 0px;
	padding: 0px;
	font-size: 14px;
}
h1,h2,h3,h4,h5,h6{
	font-weight: 300;
}
li,ol{
	/*float: left;*/
	list-style: none;
}
a{
	text-decoration: none;
}
.content{
	padding: 60px 0px;
}
.text-center{
	text-align: center;
}
.row{
	margin-left: -15px;
	margin-right: -15px;
}
.border{
	width: 80px;
	height: 2px;
	margin: 0 auto;
	background: #71be3c;
}
.col-lg-4{
	display: block;
	float: left;
	position: relative;
	top: 10%;
	padding-left: 15px;
	padding-right: 15px;
}
.current{
	color: #0093cf;
}
.current1{
	color: black;
}
