/************顶部开始***********/
.top{
	width: 100%;
	height: 642.8px;
	padding: 0px;
	background: url(../images/home.jpg) no-repeat;
	background-size: cover;
	background-position: center center;
}
.top .top_wrap{
	box-sizing: border-box;
	padding: 240px 0px;
	width: 72.6%;
	height: 642.8px;
	margin: auto;
	/*background: black;*/
}
.top .top_wrap .top_content{
	position: relative;
	margin-left: 15px;
	margin-right: 15px;
}
.top .top_wrap .top_content h1{
	display:block;
	width: 100%;
	font-size: 60px;
	color: white;
	margin-bottom: 20px;
	text-align: center;
}
.top .top_wrap .top_content h6{
	text-align: center;
	display: block;
	width: 110px;
	height: 30px;
	padding: 6px 9px;
	margin: auto;
	box-sizing: border-box;
	color: white;
	background: #0093cf;
	font-weight: bold;
	border-radius: 2px;
	cursor: pointer;
}

.top .top_wrap .top_content h3{
	display: block;
	width: 100%;
	font-size: 24px;
	color: white;
	text-align: center;
	margin-top: 15px;
}
@media only screen and (max-width: 960px){
	.top{
		height: 414.8px;
		overflow: hidden;
	}
	.top .top_wrap{
		height: 414.8px;
		margin-top: -170px;
	}
}
/************顶部结束***********/



/************导航开始***********/
.nav{
	width: 100%;
	height: 51px;
	background: #fff;
	z-index: 9999999;
	display: block;
	border-bottom-color: transparent;
	-webkit-box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.3);
	box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.3);
}
.nav1{
	width: 100%;
	height: 51px;
	background: #fff;
	display: none;
}
.nav .navWrap{
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	padding: 0px 180px;
}
.nav .navWrap .navTitle{
	height: 50px;
	line-height: 50px;
	font-size: 18px;
	float: left;
	font-weight: 600;
	color: #777;
}
.nav .navWrap ul{
	float: right;
}
.nav .navWrap ul li{
	float: left;
	width: 75.36px;
	height: 50px;
	line-height: 50px;
	font-weight: 600;
	text-align: center;
	/*color: #777;*/
	cursor: pointer;
}
.nav .navWrap ul .oLi{
	color: #777;
}
.nav .navWrap ul li:nth-child(1){
	/*color: #0093cf;*/
}
.nav .navWrap ul li:nth-child(2){
	width: 96.76px;
}
.nav .navWrap ul li:nth-child(3){
	width: 76px;
}
.nav .navWrap ul li:nth-child(4){
	width: 88.84px;
}
.nav .navWrap ul li:nth-child(5){
	width: 81.19px;
}
.nav .navWrap ul li:nth-child(6){
	width: 100.24px;
}
/************导航结束***********/

