.carousel{
	position: relative;
	overflow: hidden;
}
.pg3 .pg3Wrap .col-lg-12 .carousel .carousel-inner{
	position: fixed;
	position: relative;
	/*overflow: hidden;*/
	width: 100%;
	/*transform: translate(-2160px,0px);*/
	transition: transform 0.5s linear;
}
.pg3 .pg3Wrap .col-lg-12 .carousel .carousel-inner .item{
	/*display: inline-block;*/
	/*float: left;*/
	width: 100%;
	height: 100%;
	/*color: black;*/
	margin-bottom: 30px;
	z-index: 999;
	position: relative;
	/*transform: translate(100px,0px);*/
	/*left: 100px;*/
	/*left: -1000px;*/
}
.pg3 .pg3Wrap .col-lg-12 .carousel .carousel-inner .item:nth-child(1){
	/*position: absolute;
	left: 1%;
	top: 0px;*/
	/*background: red;*/
	/*transform: translate(-1080px,0px);*/
}
.pg3 .pg3Wrap .col-lg-12 .carousel .carousel-inner .item:nth-child(2){
	/*color: white;*/
	position: absolute;
	left:100%;
	top: 0px;
}
.pg3 .pg3Wrap .col-lg-12 .carousel .carousel-inner .item:nth-child(3){
	position: absolute;
	left: 200%;
	top: 0px;
}
.pg3 .pg3Wrap .col-lg-12 .carousel .carousel-inner .item:nth-child(4){
	position: absolute;
	left: 300%;
	top: 0px;
}
.pg3 .quote{
