	margin-right: auto;
}
.pg3 .carousel ul li{
	display: inline-block;
	width: 7px;
	height: 7px;
	border: 1px solid #a6a6a6;
	cursor: pointer;
	/*line-height: 7px;*/
	border-radius: 3px;
	transition: border 0.2s linear;
}

.pg3 .carousel ul .current2{
